import { Component, OnInit } from '@angular/core';
import {  EventEmitter, Input, Output } from '@angular/core';
import { NgbDate, NgbCalendar, NgbDatepickerModule, NgbDateStruct, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
@Component({
  selector: 'app-date-picker',
  templateUrl: './date-picker.component.html',
  styleUrls: ['./date-picker.component.scss']
})
export class DatePickerComponent implements OnInit {
  hoveredDate: NgbDate | null = null;
  fromDate!: NgbDate | null;
  toDate!: NgbDate | null;
  selectedFromDate!: string | null;
  selectedToDate!: string | null;
  @Output() public onFromDateSelected = new EventEmitter();
  @Output() public onToDateSelected = new EventEmitter();
  @Output() public OnDateType = new EventEmitter<string>();
  constructor(private calendar: NgbCalendar, public formatter: NgbDateParserFormatter) {
   
      this.fromDate = calendar.getToday();
      this.toDate = calendar.getNext(calendar.getToday(), 'd', 10);
  }
  ngOnInit(): void {
    throw new Error('Method not implemented.');
  }
  onDateSelection(date: NgbDate) {
    if (!this.fromDate && !this.toDate) {
      this.fromDate = date;
    } else if (this.fromDate && !this.toDate && date && date.after(this.fromDate)) {
      this.toDate = date;
    } else {
      this.toDate = null;
      this.fromDate = date;
    }
    let from = this.fromDate != null ? `${this.fromDate.month}/${this.fromDate.day}/${this.fromDate.year}` : null;
    // let to = this.toDate != null ? new Date(this.toDate!.year, this.toDate!.month - 1, this.toDate!.day) : null;
    let to = this.toDate != null ? `${this.toDate.month}/${this.toDate.day}/${this.toDate.year}` : null;
    this.onFromDateSelected.emit(from);
    this.onToDateSelected.emit(to);
    this.OnDateType.emit('G');
  }
  isHovered(date: NgbDate) {
    return (
      this.fromDate && !this.toDate && this.hoveredDate && date.after(this.fromDate) && date.before(this.hoveredDate)
    );
  }
  isInside(date: NgbDate) {
    return this.toDate && date.after(this.fromDate) && date.before(this.toDate);
  }
  isRange(date: NgbDate) {
    return (
      date.equals(this.fromDate) ||
      (this.toDate && date.equals(this.toDate)) ||
      this.isInside(date) ||
      this.isHovered(date)
    );
  }
  validateInput(currentValue: NgbDate | null, input: string): NgbDate | null {
    const parsed = this.formatter.parse(input);
    return parsed && this.calendar.isValid(NgbDate.from(parsed)) ? NgbDate.from(parsed) : currentValue;
  }
}